import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function operatingProfitMarginTtm(tableName: string) {
  const title = "Operating Margin (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["operatingProfitMarginTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Profit after operating expenses.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about operating margin?`,
              value: info.row.original.financialData.companyRatios?.data?.operatingProfitMarginTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyRatios?.data?.operatingProfitMarginTtm ?? 0) *
            100
          }
        />
      )
    },
  })
}

export { operatingProfitMarginTtm }
